import React, { useState } from 'react';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import airlines from './Airlines';
import airportNames from './airportNames'; // Asegrate de importar el objeto aqu

function Amadeus() {
  const [clientName, setClientName] = useState('');
  const [inputs, setInputs] = useState(['']);
  const [prices, setPrices] = useState(['']);
  const [flights, setFlights] = useState([]);

  const handleClientNameChange = (e) => {
    setClientName(e.target.value);
  };

  const handleInputChange = (index, e) => {
    const newInputs = [...inputs];
    newInputs[index] = e.target.value;
    setInputs(newInputs);
  };

  const handlePriceChange = (index, e) => {
    const newPrices = [...prices];
    newPrices[index] = e.target.value;
    setPrices(newPrices);
  };

  const handleAddOption = () => {
    setInputs([...inputs, '']);
    setPrices([...prices, '']);
  };

  const preprocessData = (data) => {
    return data.split('\n').map(line => {
      const routeMatch = /(\d{2}\w{3})\s+(\d)\s+(\w{3})(\w{3})/.exec(line);
      if (routeMatch && !line.includes('*')) {
        line = line.replace(routeMatch[0], `${routeMatch[1]} ${routeMatch[2]}*${routeMatch[3]}${routeMatch[4]}`);
        console.log(`Preprocessed line (route): "${line}"`);
      }

      const timeMatch = /(\d{3,4}[AP]M?)(\d{3,4}[AP]M?)/.exec(line);
      if (timeMatch) {
        line = line.replace(timeMatch[0], `${timeMatch[1]} ${timeMatch[2]}`);
        console.log(`Preprocessed line (time): "${line}"`);
      }

      return line;
    }).join('\n');
  };

  const handleProcess = () => {
    const regex = /(\d+)\s+(\w{2})\s*(\d+)\s+(\w)\s+(\d{2}\w{3})\s*\d?\*?(\w{3})(\w{3})\s+(\w{2}\d?)/g;

    const allFlightData = inputs.map((input, optionIndex) => {
      input = preprocessData(input); // Preprocesar los datos
      const lines = input.split('\n');
      const optionData = [];

      lines.forEach((line) => {
        const match = regex.exec(line);
        if (match) {
          console.log('Regex match:', match);
          const [, , airlineCode, flightNumber, bookingClass, depDate, depAirport, arrAirport] = match;
          
          // Extract times and arrival date
          const timeMatch = line.match(/(\d{1,4}[AP]M?)\s+(\d{1,4}[AP]M?)\s+(\d{2}\w{3})/);
          if (timeMatch) {
            const [ , rawDepTime, rawArrTime, arrDate ] = timeMatch;
            const depTime = convertTo24Hour(rawDepTime);
            const arrTime = convertTo24Hour(rawArrTime);
            console.log(`Raw departure time: ${rawDepTime}, Raw arrival time: ${rawArrTime}`);
            console.log(`Converted departure time: ${depTime}, Converted arrival time: ${arrTime}`);
            
            const additionalServices = lines.join(' ');
            const includesCheckedBag = /checked\sbag/i.test(additionalServices);

            optionData.push({
              option: optionIndex + 1,
              airlineName: getAirlineName(airlineCode),
              airlineLogo: getAirlineLogo(airlineCode),
              flightNumber,
              bookingClass,
              depDate,
              arrDate,
              depAirport,
              arrAirport,
              depTime,
              arrTime,
              includesCheckedBag
            });
          }
        } else {
          console.log('No match for line:', line);
        }
      });

      return optionData;
    });

    console.log('All flight data:', allFlightData);
    setFlights(allFlightData);
  };

  const convertTo24Hour = (time) => {
    if (!time) return '';

    let period = time.slice(-1).toUpperCase(); // AM or PM
    let timeWithoutPeriod = time.slice(0, -1).padStart(4, '0');

    let hours = parseInt(timeWithoutPeriod.slice(0, -2), 10);
    let minutes = timeWithoutPeriod.slice(-2);

    if (period === 'P' && hours !== 12) {
      hours += 12;
    } else if (period === 'A' && hours === 12) {
      hours = 0;
    }

    return hours.toString().padStart(2, '0') + ':' + minutes;
  };

  const getAirlineName = (code) => {
    return airlines[code]?.name || code;
  };

  const getAirlineLogo = (code) => {
    return airlines[code]?.logo || null;
  };

  const getAirportName = (code) => {
    return airportNames[code] || code;
  };

  const formatDate = (date) => {
    const months = [
      'enero', 'febrero', 'marzo', 'abril', 'mayo', 'junio',
      'julio', 'agosto', 'septiembre', 'octubre', 'noviembre', 'diciembre'
    ];
    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();
    return `San Salvador, ${day} de ${month} ${year}`;
  };

  const formatPrice = (price) => {
    // Cambiar el formato de precio para que el smbolo de dlar ($) aparezca antes de la cantidad
    return `$${new Intl.NumberFormat('es-ES', { style: 'decimal', minimumFractionDigits: 2 }).format(price)}`;
  };

  const exportToPDF = () => {
    const doc = new jsPDF({
      unit: 'mm',
      format: 'a4',
      compress: true
    });

    const pageWidth = doc.internal.pageSize.getWidth();
    const margin = 5; // Margen derecho e izquierdo de 5 mm
    const usablePageWidth = pageWidth - 2 * margin;

    const currentDate = formatDate(new Date());

    // Ttulo del documento
    doc.setFont('Calibri', 'bold');
    doc.setFontSize(16);
    doc.text('Cotizacin Boletos Areos', pageWidth / 2, 20, null, null, 'center');

    // Agrega el logo al PDF en la esquina superior derecha
    const logoUrl = process.env.PUBLIC_URL + '/logoescamilla.jpg';
    const barraImagen = process.env.PUBLIC_URL + '/barra_encabezado_pagina.jpg';

    if (barraImagen) {
      doc.addImage(barraImagen, 'JPEG', 10, 10, usablePageWidth, 10);
    }

    if (logoUrl) {
      doc.addImage(logoUrl, 'JPEG', 150, 10, 50, 40); // Tamao reducido del logo de la aerolnea en un 30%
    }

    doc.setFontSize(11); // Incrementa el tamao de la fecha
    doc.text(currentDate, margin, 50);
    doc.setFontSize(14);
    doc.setFont('Calibri', 'normal');
    doc.text(`Cotizacin para: ${clientName}`, margin, 60);
    doc.setFontSize(12);
    doc.text(
      'Es un gusto el poder apoyarle para hacer de su prximo viaje toda una experiencia inolvidable!',
      margin, 70,
      { maxWidth: usablePageWidth }
    );
    doc.text(
      'Adjuntamos la cotizacin de boletos areos; tomando en cuenta las especificaciones por usted planteadas, todos los cupos y precios estn sujetos a disponibilidad al momento de la confirmacin del boleto.',
      margin, 80,
      { maxWidth: usablePageWidth }
    );
    doc.text('Quedamos a sus rdenes por cualquier duda / requerimiento adicional.', margin, 90, { maxWidth: usablePageWidth });

    let finalY = 100;

    flights.forEach((optionData, optionIndex) => {
      const tableStartY = finalY + 15; // Ajustar el espacio entre el texto y la tabla
      if (tableStartY + 10 > doc.internal.pageSize.height) {
        doc.addPage();
        finalY = 20; // Ajustar el espacio entre el texto y la tabla en una nueva pgina
      }

      doc.text(`Opcin ${optionIndex + 1}`, margin, finalY + 10);
      doc.setFont('bold');
      // Cambiar el formato de precio para que el smbolo de dlar ($) aparezca antes de la cantidad
      doc.text(`Precio de boleto areo con impuestos incluidos: ${formatPrice(prices[optionIndex])}`, margin, finalY + 15);
      doc.setFont('normal');
      doc.autoTable({
        startY: finalY + 20, // Ajustar el espacio entre el texto y la tabla
        margin: { left: margin, right: margin },
        head: [['Lnea Area', '# Vuelo', 'Clase', 'Fecha de Salida', 'Fecha de Llegada', 'Ruta', 'Hora de Salida', 'Hora de Llegada', 'Maleta de Bodega']],
        body: optionData.map(flight => [
          { content: '', img: flight.airlineLogo, width: 10.5, height: 7 }, // Tamao reducido del logo en 30%
          flight.flightNumber,
          flight.bookingClass,
          flight.depDate,
          flight.arrDate, // Fecha de llegada
          `${getAirportName(flight.depAirport)} - ${getAirportName(flight.arrAirport)}`, // Mostrar la ruta
          flight.depTime,
          flight.arrTime,
          flight.includesCheckedBag ? 'S' : 'No'
        ]),
        didDrawCell: (data) => {
          if (data.column.index === 0 && data.cell.section === 'body') {
            doc.addImage(data.cell.raw.img, 'PNG', data.cell.x + 1, data.cell.y + 1, 10.5, 7);
          }
        },
        styles: {
          fontSize: 8,
          cellPadding: 3,
          font: "courier",
        },
        headStyles: {
          fillColor: [0, 0, 128],
          textColor: [255, 255, 255],
          fontStyle: "bold"
        },
        bodyStyles: {
          fillColor: [240, 240, 240],
        },
        alternateRowStyles: {
          fillColor: [255, 255, 255]
        },
        pageBreak: 'avoid'
      });

      finalY = doc.lastAutoTable.finalY + 10;

      if (finalY > doc.internal.pageSize.height - 30) {
        doc.addPage();
        finalY = 10;
      }
    });

    doc.setFontSize(12);
    doc.text('Favor tomar en cuenta:', margin, finalY + 10); // Ttulo de las notas
    const notes = [
      ' La tarifa se garantiza una vez emitido el boleto.',
      ' Previo a emisin de boleto, tarifa sujeta a cambio sin previo aviso.',
      ' Boleto no endosable, ni transferible. Tarifa no reembolsable.',
      ' Aplica penalidad por cambio de fecha ms nivelacin de tarifa.',
      ' Boleto puede ser anulado solo el mismo da de la emisin.',
      ' El valor del costo del equipaje no est incluido en el precio total de la tarifa area.',
      ' Previo a su viaje, le recomendamos revisar los siguientes documentos: Pasaporte con vigencia mnima de 6 meses previa al inicio del viaje o visa. Tarjeta de Asistencia al Viajero (compra opcional) Favor revisar y confirmar.'
    ];
    notes.forEach((note, i) => {
      if (finalY + 20 + (i * 6) > doc.internal.pageSize.height - 10) {
        doc.addPage();
        finalY = 10;
      }
      doc.text(note, margin, finalY + 20 + (i * 6), { maxWidth: usablePageWidth }); // Ajusta el interlineado a 6
    });

    doc.save('cotizacion.pdf');
  };

  return (
    <div>
      <h2>Formato Amadeus</h2>
      <label>
        Nombre del Cliente:
        <input
          type="text"
          value={clientName}
          onChange={handleClientNameChange}
          placeholder="Ingresa el nombre del cliente"
        />
      </label>
      <br /><br /> {/* Espacio adicional entre nombre y el primer cuadro */}
      {inputs.map((input, index) => (
        <div key={index}>
          <textarea
            value={input}
            onChange={(e) => handleInputChange(index, e)}
            placeholder={`Pega aqu el formato de Amadeus, opcin ${index + 1}`}
            rows="10"
            cols="50"
          />
          <br />
          <label>
            Precio:
            <input
              type="number"
              value={prices[index]}
              onChange={(e) => handlePriceChange(index, e)}
              placeholder="Ingresa el precio"
            />
          </label>
        </div>
      ))}
      <br />
      <button onClick={handleAddOption}>Agregar Opcin</button>
      <button onClick={handleProcess}>Procesar</button>
      <button onClick={exportToPDF}>Exportar a PDF</button>
      <div>
        {flights.length === 0 ? (
          <p>No se encontraron datos de vuelo.</p>
        ) : (
          flights.map((optionData, optionIndex) => (
            <div key={optionIndex}>
              <h3>Opcin {optionIndex + 1}</h3>
              <p><strong>Precio de boleto areo con impuestos incluidos:</strong> {formatPrice(prices[optionIndex])}</p>
              <table border="1">
                <thead>
                  <tr>
                    <th>Lnea Area</th>
                    <th># Vuelo</th>
                    <th>Clase</th>
                    <th>Fecha de Salida</th>
                    <th>Fecha de Llegada</th>
                    <th>Ruta</th>
                    <th>Hora de Salida</th>
                    <th>Hora de Llegada</th>
                    <th>Incluye Maleta de Bodega</th>
                  </tr>
                </thead>
                <tbody>
                  {optionData.map((flight, index) => (
                    <tr key={index}>
                      <td><img src={flight.airlineLogo} alt={flight.airlineName} width="35" /></td> {/* Tamao reducido del logo en 30% */}
                      <td>{flight.flightNumber}</td>
                      <td>{flight.bookingClass}</td>
                      <td>{flight.depDate}</td>
                      <td>{flight.arrDate}</td> {/* Fecha de llegada */}
                      <td>{`${getAirportName(flight.depAirport)} - ${getAirportName(flight.arrAirport)}`}</td> {/* Ruta */}
                      <td>{flight.depTime}</td>
                      <td>{flight.arrTime}</td>
                      <td>{flight.includesCheckedBag ? 'S' : 'No'}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default Amadeus;
